import React,{useState} from "react"

export default function NodeReact(){

    const [req,setReq]=useState("")
    const [api,setApi]=useState("Aguardando requisicao")
  
    const parametro = ()=>{
        let parametro = document.querySelector("#curso").value
        console.log(parametro)
        let pesquisado = parametro
        let endpoint = `http://localhost:3001/${pesquisado.toLocaleLowerCase()}`
        console.log(endpoint)

        gravarDados(endpoint,pesquisado)
    }
    const gravarDados = (endpoint,pesquisado)=>{
        fetch(endpoint ,{method: "Post"} )
        .then(res=>res.json())
        .then(dados=>{
            if(dados.curso){
                setApi(dados.curso)
            }
            else{
                setApi(dados.erro +"<p> curso pesquisado </p> : "+pesquisado)
            }
        })
        .catch(()=>{
            setApi("Erro com o servidor")
        })
    }

    return(
        <section>
            <input type="text" name="curso" id="curso" onChange={(e)=>setReq(e.target.value)}/>
            <button id="btn_curso" onClick={()=>{parametro(req)}}>Escolher curso</button>
            <div id="API">{api}</div>
        </section>
    )
}
