import React from 'react'
import Dia from './imgs/dia.jpg'
import Noite from './imgs/noite.jpg'

export default function Saudacao(props) {

    return(
        <>
            <h1>Eventos em React</h1>
            <img src={props.saudacao?Dia:Noite} style={{width:'350px'}}/>
            <br />
            <button
                onClick={()=>props.setSaudacao(!props.saudacao)}>
                {props.saudacao?'Desligar':'Ligar'}
            </button>
        </>
    )
}