import React from 'react'


export default function Dados(props){
    let v1 = 10
    let v2 = 5

    return(
        <section>
            <h1>Enviar propriedades para os componentes com PROPS</h1>
            <h1>Usando CSS em React, formas de usar CSS em React</h1>

            <p>Canal:{props.Canal()}</p>
            <p>Youtube:{props.Youtube}</p>
            <p>Curso:{props.Curso}</p>
            <p>{'A soma entre ' + v1 + ' e ' + v2 + ' e igual a ' + props.Soma(10,5)}</p>
        </section>
    )

}