import React from "react"

export default class Carro extends React.Component{
    constructor(props){
        super(props)
        this.modelo="HRV"

        this.state={
            ligado:false,
            velAtual:0
        }

        this.ld=this.ligarDesligar.bind(this)
        
    }

    ligarDesligar(){
        //Tem que chamar o setState para renderizar, se usar apenas o state muda mas nao renderiza de novo
        //this.state.ligado=true
        //this.setState({ligado:!this.state.ligado})
        
        //Usando arrow function
        this.setState(
            (state)=>(
                {ligado:!state.ligado}
            )
        )
        
        /*
        //Usando funcao anonima traducional
        this.setState(
            function(state){
                return{
                    ligado:!state.ligado
                }
            }
        )*/


    }
    acelerar(){
        this.setState(
            (state)=>(
                {velAtual:state.velAtual+this.props.fator}
            )
        )
            
    }
    componentDidMount(){
        console.log("O carro foi carregado")
    }

    componentDidUpdate(){
        console.log("O carro foi atualizado")
    }
    
    componentWillUnmount(){
        console.log("O carro foi removido")
    }
    
    render(){
        return(

            <div>
                <h1>STATE em Componentes de Classe ReactJS</h1>
                <h1>Meu Carro</h1>
                <p>Modelo: {this.modelo}</p>
                <p>Ligado:{this.state.ligado ? 'Sim' : 'Nao'}</p>
                <p>Vel. Atual:{this.state.velAtual}</p>
                <button onClick={()=>this.ligarDesligar()}>
                    {this.state.ligado ? 'Desligar' : 'Ligar carro'}
                </button>
                <button onClick={()=>this.acelerar()}>
                    Acelerar
                </button>
                <h1>Usando o metodo Bind</h1>
                <button onClick={this.ld}>
                    {this.state.ligado ? 'Desligar' : 'Ligar carro'}
                </button>
            </div>
        )
    }

}