import React from 'react'
import Dados from './Dados'
import '../App.css'

export default function Corpo(){
    //Propiedades com props
    const cnl=()=>{
        return 'CFB Cursos'
    }
    const yt = 'youtube.com/cfbcursos'
    const crs = 'React.js'
    const soma=(v1,v2)=>{
        return v1+v2
    }

    //Objeto
    const textoDestaque={
        color:'green',
        fontSize:'3em'
    }


    return(
        <section className='conteiner'>
            <h1>Como criar componentes em React</h1>
            <header>
                <h2 style={{color:'red', fontSize:'2m'}}>Curso de React</h2>
                <p style={textoDestaque}>Se inscreva no canal</p>
                <p className='texto'>Clique no joinha e ative o sininho</p>
                <Dados
                    Canal={cnl}
                    Youtube={yt}
                    Curso={crs}
                    Soma={soma}
                />
            </header>
        </section>
    )

}