import React from 'react'
import Logo from './imgs/dia.jpg'

export default function Header(){
    return(
        <header className='conteiner'>
            <h1>Expresses em JSX. Insero de imagens no App. Constantes e Variveis</h1>
            <img src={Logo} alt="Bom dia"/>
            <h2>CFB Cursos</h2>
        </header>
    )

}