import React from "react"

export default function Nota(props){
    
    return(
        <div>
            <h1>Elevacao State</h1>
            <legend>Informe a sua nota{props.num}:</legend>
            <input type="number" name={'znota'+props.num} value={props.notas} onChange={(e)=>props.setNotas(e)} />
        </div>
    )

}

