import React,{useState,useEffect} from 'react'
import Header from './Componentes/Header'
import Corpo from './Componentes/Corpo'
import Numero from './Componentes/Numero'
import Saudacao from './Componentes/Saudacao'
import Cor from './Componentes/Renderizacao condicional'
import Carros from './Componentes/Lista usando map'
import Form from './Componentes/Formularios em react'
import Objetos from './Componentes/Manipulando um state em varios componentes'
import Nota from './Componentes/Nota'
import Resultado from './Componentes/Resultado'
import Caixa from './Componentes/Caixa'
import Canal from './Componentes/Canal'
import Classe from './Componentes/Classes'
import Carro from './Componentes/Carro'
import Globais from './Componentes/globais'
import {BrowserRouter as Router, Routes, Route, Link} from 'react-router-dom'
import Pagina1 from './Pagina1'
import Pagina2 from './Pagina2'
import Pagina3 from './Pagina3'
import Tabela from './Componentes/Classe TabelaIMC'
import Calculadora from './Componentes/Calculadora react'
import NodeReact from './Componentes/node&React'

export default function App() {
  const [num,setNum]=useState(200)
  const [nome,setNome]=useState('Alex')

  //Ligar/desligar a luz
  const [saudacao,setSaudacao]=useState(true)

  //cancelar link
  const cancelar=(obj)=>{
    return obj.preventDefault()
  }

  //states para as notas
  const [notas,setNotas]=useState({"nota1":"0","nota2":"0","nota3":"0","nota4":"0"})

  //somando as notas
  const handlesetNotas=(e)=>{
    if(e.target.getAttribute("name") =="znota1"){
        setNotas({"nota1":e.target.value,"nota2":notas.nota2,"nota3":notas.nota3,"nota4":notas.nota4})
    }else if(e.target.getAttribute("name")=="znota2"){
        setNotas({"nota1":notas.nota1,"nota2":e.target.value,"nota3":notas.nota3,"nota4":notas.nota4})
    }else if(e.target.getAttribute("name")=="znota3"){
        setNotas({"nota1":notas.nota1,"nota2":notas.nota2,"nota3":e.target.value,"nota4":notas.nota4})
    }else if(e.target.getAttribute("name")=="znota4"){
        setNotas({"nota1":notas.nota1,"nota2":notas.nota2,"nota3":notas.nota3,"nota4":e.target.value})  
    }
  }

  //Armazenamento local
  const [nomels,setNomels]=useState()

  const armazenar=(chave,valor)=>{
    localStorage.setItem(chave,valor)
  }
  const consultar=(chave,valor)=>{
    alert(localStorage.getItem(chave,valor))
  }
  const remover=(chave,valor)=>{
    localStorage.removeItem(chave,valor)
  }

  const [carro,setCarro]=useState(true)
  //Ciclo de vida de um componente(componentWillUnmount())
  const ocultar=()=>{
    setCarro(!carro)
  }
  //Manipulando variaveis globais
  const [resumo,setResumo]=useState(Globais.resumo)
  
  const gravarResumo=()=>{
    Globais.resumo=resumo
  }

  const verResumo=()=>{
    alert(Globais.resumo)
  }
  
  //Exercicio de tabela React
  const carrostab=[
    {categoria:"Esporte",preco:"105000",modelo:"HRV"},
    {categoria:"Esporte",preco:"220000",modelo:"Golf GTI"},
    {categoria:"SUV",preco:"85000",modelo:"Camaro"},
    {categoria:"SUV",preco:"115000",modelo:"T-Cross"},
    {categoria:"Utilitario",preco:"155000",modelo:"Hilux"},
    {categoria:"Utilitario",preco:"190000",modelo:"Ranger"},
  ]

  const [categoria,setCategoria]=useState('')

  const linhas=(cat)=>{
    const li=[]
    carrostab.forEach((carro)=>{
      if(carro.categoria.toUpperCase() == cat.toUpperCase() || cat == ''){
          li.push(<tr>
                      <td>{carro.categoria}</td>
                      <td>{carro.preco}</td>
                      <td>{carro.modelo}</td>
                    </tr>) 
      }      
    });
    return li
  }
  
  const Pesquisatabela=(cat,scat)=>{
    return(
      <div>
        <label htmlFor="tbpesq">Digite uma categoria:</label>
        <input type="text" id='tbpesq' value={cat} onChange={(e)=>{scat(e.target.value)}}/>        
      </div>
    )

  }


  //Exercicio Calculadora IMC
  const [peso,setPeso]=useState(0)
  const [altura,setAltura]=useState(0)
  const [IMC,setIMC]=useState(0)

  const pesoCalc=(pes,speso)=>{
    return(
      <p>
        <label htmlFor="ipeso">Informe o seu peso: </label>
        <input type="text" id='ipeso' value={pes} onChange={(e)=>speso(e.target.value)} />   
      </p>
    )
  }
  const alturaCalc=(alt,salt)=>{
    return(
      <p>
        <label htmlFor="ialtura">Informe sua altura: </label>
        <input type="text" id='ialtura' value={alt} onChange={(e)=>{salt(e.target.value)}} /> 
      </p>
    )
  }
  

  const calcIMC=(p,a,si)=>{
    let res=(p/(a*a))
    si(res.toFixed(2))
  }

  let a = "2*(4+6)"

  return(
    <section className='conteiner'>
      <header>
        <Link  to='/' >Home</Link>
        <Link  to='/pag1' >Pagina 1</Link>
        <Link  to='/pag2' >Pagina 2</Link>
        <Link  to='/pag3' >Pagina 3</Link>
      </header>
      <main>
        <Routes>
          <Route path='/pag1' element={<Pagina1/>} />
          <Route path='/pag2' element={<Pagina2/>} />
          <Route path='/pag3' element={<Pagina3/>} />
        </Routes>
      </main>
      <Header/>
      <Corpo/>
      <h1>O que  State e como usar State em React</h1>
      <p>O valor do state num em App: {num}</p>
      <Numero num={num} setNum={setNum} />
      <p>{nome}</p>
      <Saudacao saudacao={saudacao} setSaudacao={setSaudacao}/>
      <p><a href="https://youtube.com/cfbcursos" target="blanc" onClick={(e)=>cancelar(e)}>CFB cursos</a></p>
    
      <Cor/>

      <Carros/>

      <Form/>

      <Objetos/>

      <Nota num={1} notas={notas.nota1} setNotas={handlesetNotas} />
      <Nota num={2} notas={notas.nota2} setNotas={handlesetNotas} />
      <Nota num={3} notas={notas.nota3} setNotas={handlesetNotas} />
      <Nota num={4} notas={notas.nota4} setNotas={handlesetNotas} />

      <Resultado resultado={parseFloat(notas.nota1)+parseFloat(notas.nota2)+parseFloat(notas.nota3)+parseFloat(notas.nota4)}/>

      <Caixa>
        <Canal/>  
        <p>Curso de react</p>
      </Caixa>

      <h1>LocalStorage em React</h1>

      <label htmlFor="nomels">Digite um nome</label><br />
      <input type="text" name="" id="nomels" value={nomels} onChange={(e)=>setNomels(e.target.value)} /><br/>
      <button onClick={()=>armazenar('ls_nome',nomels)}>Gravar nome</button>
      <button onClick={()=>consultar('ls_nome',nomels)}>Ver nome</button>
      <button  id='classe' onClick={()=>remover('ls_nome',nomels)}>Remover nome</button>

      <h1>Criando Componentes de Classe em react</h1>
      <Classe canal='CFB Cursos'/>
      <button onClick={()=>{ocultar()}}>Ocultar</button>
       {carro ? <Carro fator={1}/> : ''}
       <p><img src="imgs\Ciclo de vida de um componente.jpg" alt="" /></p>
      <h1>Ciclo de Vida dos componentes em REACT</h1>
      <h1>Variveis Globais Static em React</h1>
      <input type="text" value={resumo} size='100' onChange={(e)=>setResumo(e.target.value)}/><br/>
      <hr />
      <button onClick={()=>gravarResumo()}>Gravar resumo</button>
      <hr />
      <button onClick={()=>verResumo()}>Ver Resumo</button>
      
      <hr />
      
      <h1>Exercicio de tabela</h1>
      {Pesquisatabela(categoria,setCategoria)}
      <table>
      <thead>
            <tr>
                <th scope="col" style= {{width:"50px"}}>Categoria</th>
                <th scope="col" style= {{width:"50px"}} >Preo</th>
                <th scope="col" style= {{width:"50px"}} >Modelo</th>
            </tr>
        </thead>
        <tbody>
          {linhas(categoria)}
        </tbody>
      </table>
      <br />
      <h1>Calculadora de peso IMC</h1>
      {pesoCalc(peso,setPeso)}
      {alturaCalc(altura,setAltura)}
      <button onClick={()=>{calcIMC(peso,altura,setIMC)}}>Calcular</button>
      <p>O seu IMC e de: {IMC}</p>
      <table>
        <thead>
              <tr>
                  <th scope="col" style= {{width:"50px"}}>Classificao</th>
                  <th scope="col" style= {{width:"50px"}} >IMC</th>
              </tr>
        </thead>
        <tbody>
          <tr>
            <td>Abaixo do peso</td>
            <td>Abaixo de 18,5</td>
          </tr>
          <tr>
            <td>Peso normal</td>
            <td>Entre 18,5 e 24,9</td>
          </tr>
          <tr>
            <td>Sobrepeso</td>
            <td>Entre 25 e 29,9</td>
          </tr>
          <tr>
            <td>Obesidade Grau I</td>
            <td>Entre 30 e 34,9</td>
          </tr>
          <tr>
            <td>Obesidade Grau II</td>
            <td>Entre 35 e 39,9</td>
          </tr>
          <tr>
            <td>Obesidade Grau III ou morbida</td>
            <td>Maior que 40</td>
          </tr>
        </tbody>
      </table>
      <Tabela/>
      <Calculadora/>
      {console.log(a)}
      <NodeReact/>
    </section>
  )
}  